import React, {Component} from 'react';
//import ContactText from '../docs/ContactText.js';
import '../styles/App.scss';
import { InlineIcon } from '@iconify/react';
import githubIcon from '@iconify/icons-simple-icons/github';
import linkedinIcon from '@iconify/icons-simple-icons/linkedin';
import { ReactTypeformEmbed } from 'react-typeform-embed';
import GradientButton from './GradientButton';
import Button from 'react-bootstrap/Button'
export default class Contact extends Component {
    constructor(props) {
        super(props);
        this.openForm = this.openForm.bind(this);
      }
    
      openForm() {
        this.typeformEmbed.typeform.open();
      }

    render() {  
        
        return(
                <section id="contact">
                    
                
                    <div className="ExamplePopup">
                  <ReactTypeformEmbed
                    popup
                    autoOpen={false}
                    url="https://bryanmarevalo.typeform.com/to/x7OOW6JK"
                    hideHeaders
                    hideFooter
                    buttonText="Go!"
                    style={{ top: 100 }}
                    ref={tf => {
                      this.typeformEmbed = tf;
                    }}
                  />
                    
                    
                  </div>
                  <Button className="contact-btn" onClick={this.openForm}>Let's Connect</Button>
                  
                <div>
                <a href="https://github.com/" target="_blank" rel="noopener noreferrer"><InlineIcon icon={githubIcon} className="contact-icon" /></a>
                <a href="https://github.com/" target="_blank" rel="noopener noreferrer"><InlineIcon icon={linkedinIcon} className="contact-icon" /></a>
                </div>
                
              
                
            </section>      
        )
    }
}