import React, {Component} from 'react'
//import logo from '../assets/B.png'
//import logo from '../assets/B.svg'
import heart from '../assets/heart.svg'
// import heart2 from '../assets/heart2.svg'
import '../styles/App.scss';
import ThemeSwitcher from './ThemeSwitcher.js'
import { Navbar,Nav } from 'react-bootstrap'
// import { BrowserRouter as Router, Route, Link } from 'react-router-dom'

import { InlineIcon } from '@iconify/react';
import githubIcon from '@iconify/icons-simple-icons/github';
import linkedinIcon from '@iconify/icons-simple-icons/linkedin';
import arrowUpOutlined from '@iconify/icons-ant-design/arrow-up-outlined';

export default class BootStrapNav extends Component {
    constructor() {
        super()
        this.state = {
            sectid: '',
            key: '',
            
            
        }
        this.complexOnSelect = this.complexOnSelect.bind(this)
        this.handleUpArrow = this.handleUpArrow.bind(this)
    }

    
    handleUpArrow(e) {
        console.log(e);
        console.log(e);

        
        if(e === 1){
            window.location.hash = "/";
            
            this.setState({
                sectid: '/',
                key: 0,
                
            })
            
        }
        else if(e === 2){
            window.location.hash = "#home";
            this.setState({
                sectid: '#home',
                key: 1
            })
        }
        else if(e === 3){
            window.location.hash = "#about";
            this.setState({
                sectid: '#about',
                key: 2
            })
        }
        else if(e === 4){
            window.location.hash = "#projects";
            this.setState({
                sectid: '#projects',
                key: 3
            })
        }
        else if(e === 5){
            window.location.hash = "#experience";
            this.setState({
                sectid: '#experience',
                key: 4
            })
        }
        else if(e === 6){
            window.location.hash = "#services";
            this.setState({
                sectid: '#services',
                key: 5
            })
        }
        
    }

    complexOnSelect(eventKey, syntheticEvent) {
        if (eventKey === 1) {
          this.setState({
              sectid: '#home',
              key: 1
          })
        }
        else if (eventKey === 2) {
            this.setState({
                sectid: '#about',
                key: 2
            })
        }
        else if (eventKey === 3) {
            this.setState({
                sectid: '#projects',
                key: 3
            })
        }
        else if (eventKey === 4) {
            this.setState({
                sectid: '#experiences',
                key:4
            })
        }
        else if (eventKey === 5) {
            this.setState({
                sectid: '#services',
                key:5
            })
        }
        else if (eventKey === 6) {
            this.setState({
                sectid: '#contact',
                key: 6
            })
        }
        
    }

    
    render() {
        console.log(this.context.router);
        console.log(this.state.sectid);
        return(
            <Navbar fixed="top" collapseOnSelect expand="lg" bg="transparent" >
                <Navbar.Brand href="/">
                    <img
                        src={heart}
                        width="100"
                        height="100"
                        alt="BryAre logo"
                    />
                </Navbar.Brand> 

                <Navbar.Toggle aria-controls="responsive-navbar-nav" style={{borderColor: "#ff00b8", color:"#ff00b8"}}/>
                
                <Navbar.Collapse id="responsive-navbar-nav" style={{borderColor: "#ff00b8", color:"#ff00b8"}}>
                    <Nav 
                        style={{borderColor: "#ff00b8", color:"#ff00b8"}}
                        activeKey="#home"
                        className="mr-auto active-page"
                    >
                        <Nav.Link onSelect={() => this.complexOnSelect(1)} eventKey={1}  href="#home" className="active-page" ><p className="navbar-text active-page">Home</p></Nav.Link>
                        <Nav.Link onSelect={() => this.complexOnSelect(2)} eventKey={2} href="#about" className="active-page"><p className="navbar-text">About</p></Nav.Link>
                        <Nav.Link onSelect={() => this.complexOnSelect(3)} eventKey={3} href="#projects"className="active-page"><p className="navbar-text">Projects</p></Nav.Link>
                        <Nav.Link onSelect={() => this.complexOnSelect(4)} eventKey={4} href="#experience" className="active-page"><p className="navbar-text">Experience</p></Nav.Link>
                        <Nav.Link onSelect={() => this.complexOnSelect(5)} eventKey={5} href="#services" className="active-page"><p className="navbar-text">Services</p></Nav.Link>
                        <Nav.Link onSelect={() => this.complexOnSelect(6)} eventKey={6} href="#contact" className="active-page"><p className="navbar-text">Contact</p></Nav.Link>
                        
                    </Nav>    
                </Navbar.Collapse>                
                
                <Nav>
                    <Navbar.Collapse id="responsive-navbar-nav" >
                        <Nav className="mr-auto">

                                <Nav.Link href="https://github.com/BryAre" target="_blank" rel="noopener noreferrer"><p className="navbar-text">Github</p></Nav.Link>

                                <Nav.Link href="https://www.linkedin.com/in/bryare/" target="_blank" rel="noopener noreferrer"><p className="navbar-text">LinkedIN</p></Nav.Link>
                                <Nav.Link href="#projects"><a href = "mailto: bryanmarevalo@gmail.com" className="navbar-text">Email</a></Nav.Link>
                        </Nav>    
                    </Navbar.Collapse>  
                    
                    <ThemeSwitcher
                            cssSelector= "body"
                            darkColor=  "#090909"//"linear-gradient(to right, #4b478686 0%, rgba(79, 88, 122, 0.884) 51%, rgb(62, 62, 100) 100%);"
                            lightColor= "#f2f2f2"//"linear-gradient(to right, #c5bfc4fd 0%, rgba(137, 133, 138, 0.884) 51%, rgb(100, 100, 102) 100%);"
                            lightTextColor= "#000"
                            darkTextColor= "#fff"
                    />

                </Nav>
                {/* <a ><InlineIcon icon={arrowUpOutlined} className="icon" onClick={() => this.handleUpArrow(this.state.key)} /></a> */}
            </Navbar>
        )
    }
}

